.filtred__bottom {
  padding-top: 4px;
  padding-bottom: 4px;
  &-selected {
    display: flex;
    align-items: center;
    margin-bottom: 4px;
    &-title {
      font-size: .95rem;
      line-height: 1rem;
      font-weight: 400;
      color: var(--text-color);
      margin-right: 4px;
    }
    &-count {
      color: var(--white);
      background-color: var(--red);
      padding: 1px 4px;
      border-radius: 4px;
      font-size: .95rem;
      line-height: 1.25rem;
    }
  }

  &-swiper {
    display: flex;
    overflow: auto;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
      display: none;
    }
  }

  &-slide {
    position: relative;
    display: flex;
    align-items: center;
    background-color: var(--gray-normal);
    border-radius: 28px;
    height: 24px;
    width: auto;
    padding: 0 12px;
    cursor: pointer;
    transition: all .3s ease-in-out;
    &:not(:last-child) {
      margin-right: 4px;
    }
    &-svg {
      margin-right: 4px;
    }
    &-span {
      font-weight: 500;
      font-size: 1rem;
      line-height: 1.25rem;
      color: var(--text-color);
    }
  }
  &-btn {
    transform: scale(0);
    position: absolute;
    top: calc(50% - 6px);
    right: 8px;
    transition: transform .3s ease-in-out;
    z-index: 1;
  }

  &-slide.active-slide {
    padding: 0 28px 0 12px;
    background-color: var(--yollow);
  }

  &-slide.active-slide &-btn {
    transform: scale(1);
  }
}
